@media screen and (min-width: 820px) {
  .containerContainer {
    max-width: 400px;
  }
}

.containerContainer {
  position: relative;
}

.containerContainer ul {
  width: 100%;
  position: absolute;
  -webkit-transition: left 0.3s ease-in-out;
  -o-transition: left 0.3s ease-in-out;
  transition: left 0.3s ease-in-out;
}

.containerContainer ul:first-child {
  left: 0;
}

.containerContainer ul:nth-child(2) {
  left: 100%;
}

.projectChosen ul:first-child {
  left: -100%;
}

.projectChosen ul:nth-child(2) {
  left: 0;
}


